<template>
    <div class="content-wrap">
        <div class="search-wrap">
            <el-row
                type="flex"
                :gutter="10"
                style="height:52px;"
            >
                <el-col :span="5" >
                    <span class="item-text">手机号：</span>
                    <el-input
                        class="items"
                        placeholder="请输入手机号"
                        v-model="phone" />
                </el-col>
                <el-col :span="5" >
                    <span class="item-text">注册渠道：</span>
                    <el-select class="items" v-model="registerChanel" placeholder="注册渠道">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="5" >
                    <span class="item-text">注册时间：</span>
                    <SelectTime class="items" ref="selectRegisterTime" @selectTime="selectRegisterTime" :formatType="false" :type="'datetimerange'" />
                </el-col>
                <el-col :span="5">
                    <span class="item-text special-item">最近登录时间：</span>
                    <SelectTime class="items special" ref="selectLoginTime" @selectTime="selectLoginTime" :formatType="false" :type="'datetimerange'" />
                </el-col>
                <el-button type="primary" style="height:40px" @click="search">查询</el-button>
                <el-button style="height:40px" @click="reset">重置</el-button>
            </el-row>
        </div>
        <div class="table-wrap">
            <el-table
                :data="tableData"
                border
                :header-cell-style="{textAlign: 'center'}"
                :cell-style="{ textAlign: 'center' }"
                style="width: 100%">
                    <el-table-column
                        prop="user_id"
                        label="用户ID">
                    </el-table-column>
                    <el-table-column
                        prop="user_mobile_phone"
                        label="手机号">
                    </el-table-column>
                    <el-table-column
                        prop="image_url"
                        label="头像">
                    </el-table-column>
                    <el-table-column
                        prop="nick_name"
                        label="昵称">
                    </el-table-column>
                    <el-table-column
                        label="注册时间">
                        <template slot-scope="scope">
                            <span>{{scope.row.create_time | getTime}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="client"
                        label="注册渠道">
                    </el-table-column>
                    <el-table-column
                        label="最近登录时间">
                        <template slot-scope="scope">
                            <span>{{scope.row.login_time | getTime}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="num_of_order"
                        label="消费次数">
                    </el-table-column>
                    <el-table-column
                        prop="paid_cent"
                        label="累计消费金额">
                    </el-table-column>
                    <el-table-column
                        prop="num_of_pad"
                        label="设备数">
                    </el-table-column>
                    <el-table-column
                        label="操作">
                        <template slot-scope="scope">
							<el-button
								type="text"
								size="mini"
								@click="handleDetail(scope.row)"
								>详情</el-button
							>
						</template>
                    </el-table-column>
            </el-table>
            <Pagination 
                style="margin:10px 0 20px 0;text-align:right;"
                @handleCurrentChange="handleCurrentChange"
                @handleSizeChange="handleSizeChange"
                :total="totalNum"
                :currentPage="currentPage"
            />
        </div>
    </div>
</template>
<script>
import { userInfo } from '../../api/user';
import { parseTime } from '../../utils/index';
import { Pagination, SelectTime } from '../../components'
export default {
    data() {
        return{
            phone: '',
            registerChanel: "",
            regTime1: "",
            regTime2: "",
            loginTime1: "",
            loginTime2: "",
            options: [{
                    value: 'web',
                    label: 'Web'
                }, {
                    value: 'win',
                    label: 'PC'
                }, {
                    value: 'android',
                    label: 'Android'
                }, {
                    value: 'ios',
                    label: 'IOS'
                }
            ],
            tableData: [],
            currentPage: 1,
            totalNum: 0,
            pageSize: 10,
        }
    },
    filters: {
        getTime(time) {
            if(time) return parseTime(new Date(time));
			return '-'
		},
    },
    mounted() {
        this.getList();
    },
    methods: {
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList();
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.getList();
        },
        search() {
            this.currentPage = 1;
            this.pageSize = 10;
            this.getList()
        },
        reset() {
            this.registerChanel = '';
            this.phone  = "";
            this.$refs.selectRegisterTime.resetTimes();
            this.$refs.selectLoginTime.resetTimes()
            this.currentPage = 1;
            this.pageSize = 10;
            this.getList();
        },
        selectRegisterTime(val) {
            console.log(val)
            this.regTime1 = val.startTime;
            this.regTime2= val.endTime;
        },
        selectLoginTime(val){
            this.loginTime1 = val.startTime;
            this.loginTime2 = val.endTime;
        },
        getList() {
            let params = new Object();
            this.registerChanel ? params.registerChanel = this.registerChanel : null;
            this.regTime1 ? params.regTime1 = this.regTime1 : null;
            this.regTime2 ? params.regTime2 = this.regTime2 : null;
            this.phone ? params.phone = this.phone : null;
            this.loginTime1 ? params.loginTime1 = this.loginTime1 : null;
            this.loginTime2 ? params.loginTime2 = this.loginTime2 : null;
            params.page = this.currentPage;
            params.pageSize = this.pageSize;
            console.log(params)
            userInfo(params)
            .then(res => {
                if (res.code == 200) {
                    this.tableData = res.data.list;
                    this.totalNum = res.data.total_number;
                }
            })
            .catch(error => {
                console.log(error)
            });
        },
        handleDetail(v) {
            console.log(v)
            this.$router.push({
                path: '/user-detail',
                query: {
                    userId: v.user_id
                }
            });
            // this.$store.dispatch('order/selectedOrder', v);
        },
    },
    components: {
        Pagination, SelectTime
    }
}
</script>
<style lang="less" scoped>
.content-wrap{
    padding: 25px;
    .search-wrap{
        padding: 20px;
        border: 1px solid #ddd;
        .items{
            width: calc(100% - 100px);
        }
        .item-text{
            width: 80px;
            text-align: left;
            display: inline-block;
        }
        .special{
            width: calc(100% - 130px) !important;
        }
        .special-item{
            width: 115px !important;
        }
    }
    .table-wrap{
        margin-top: 20px;
    }
}
</style>


